.list {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    list-style-type: none;
    padding: 0;
  }
  
  .list li a {
    height: 22.5rem;
    width: 20rem;
    border: 1px solid whitesmoke;
    border-radius: 1rem;
    margin: 0.5rem;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    color: #e72929;
    font-weight: bold;
  }
  
  .image {
    object-fit: cover;
    height: 14.5rem;
  }
  
  .content {
    margin-top: 0.3rem;
    padding: 0.5rem 1rem;
    position: relative;
    height: 7.5rem;
    background-color: #ffffffbb;
  }
  
  .favorite {
    position: absolute;
    top: 0.5rem;
    right: 1rem;
    font-size: 1.2rem;
  }
  
  .favorite.not {
    color: grey;
  }
  
  .stars {
    margin: 0.5rem 0;
  }
  
  .product_item_footer {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  
  .outlet span {
    border-radius: 2rem;
    background-color: #000;
    display: inline-block;
    font-size: 0.8rem;
    color: #fff;
    margin-right: 0.3rem;
    padding: 0 0.5rem;
    margin-top: 0.2rem;
  }
  
  .outlet {
    flex: 9;
  }
  
  .cook_time {
    font-size: 0.9rem;
    flex: 3;
    text-align: right;
  }
  
  .price {
    position: absolute;
    bottom: 0;
    left: 0.9rem;
    padding: 0.3rem 100% 0 0;
    color: #008000;
    margin-top: 0.7rem;
    font-size: 1.1rem;
  }

  
.btnNext {
  position: fixed;
  top: 50%;
  left: 3%;
  border-radius: 50%;
  font-size: 15px;
  color: #e72929;
  background-color: #000;
}

.btnPrev {
  position: fixed;
  top: 50%;
  right: 3%;
  border-radius: 50%;
  font-size: 15px;
  color: #e72929;
  background-color: #000;
}

.btnNext:hover, .btnPrev:hover {
  cursor: pointer;
  transform: scale(1.2);
  transition: all 0.2s;
  box-shadow: 0 2px 2px 1px #e72929;
}